import React, { Component } from "react";
import { HEATER_KIT, PIANO_KIT } from "./data.js";

// Parent component for entire drum app
class DrumMachineApp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentDrum: "",
      currentKit: "Heater Kit",
      kitData: [...HEATER_KIT],
      volume: 75,
      loading: false,
      powerOn: false,
      showStart: true
    };
    this.handleDrumClick = this.handleDrumClick.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
    this.playDrum = this.playDrum.bind(this);
    this.handleKitSwitch = this.handleKitSwitch.bind(this);
    this.handleVolume = this.handleVolume.bind(this);
    this.highlightDrum = this.highlightDrum.bind(this);
    this.startDrums = this.startDrums.bind(this);
  }

  componentDidMount() {
    // Let user trigger drum buttons by pressing a key;
    document.addEventListener("keydown", this.handleKeyPress);
  }

  componentWillUnmount() {
    document.removeEventLister("keydown", this.handleKeyPress);
  }

  //Force user to "start" app so audio will play
  startDrums() {
    this.setState({ showStart: false, loading: true });
    setTimeout(() => {
      this.setState({ powerOn: true, loading: false });
    }, 1000);
  }

  // Handle key press on document
  handleKeyPress(event) {
    let button;
    if (this.state.powerOn) {
      //Iterate through bank array to find match
      this.state.kitData.forEach(function(drum) {
        if (event.keyCode === drum.keyCode) {
          button = document.getElementById(drum.clipName);
        }
      });
      //Click corresponding drum button
      if (button !== undefined) {
        button.click();
      }
    }
  }

  // Handle click of drum buttons
  handleDrumClick(event) {
    if (this.state.powerOn) {
      //Get audio element
      const clickSound = document.getElementById(event.target.dataset.value);
      // Get div drumpad element
      const clickButton = document.getElementById(event.target.id);
      this.playDrum(clickSound);
      this.highlightDrum(clickButton);
    }
  }

  //Play drum sound from click or key press and update display
  playDrum(sound) {
    //Update display
    this.setState({
      currentDrum: sound.getAttribute("name")
    });
    // Play Sound and make sure clip starts at 0
    sound.currentTime = 0;
    sound.volume = this.state.volume / 100;
    sound.play();
  }

  // Change color of drumPad on click and then change back
  highlightDrum(drumPad) {
    drumPad.style.backgroundColor = "#0542bc";
    setTimeout(function() {
      drumPad.style.backgroundColor = "#92959b";
    }, 150);
  }

  //Handle button blick to switch kits
  handleKitSwitch(event) {
    if (event.target.value === "Smooth Piano Kit") {
      this.setState({
        currentKit: "Smooth Piano Kit",
        kitData: [...PIANO_KIT],
        currentDrum: ""
      });
      // Change active state of buttons
      document.getElementById("piano-switch").className = "active";
      document.getElementById("heater-switch").className = "";
    } else {
      this.setState({
        currentKit: "Heater Kit",
        kitData: [...HEATER_KIT],
        currentDrum: ""
      });
      //Change active state of buttons
      document.getElementById("heater-switch").className = "active";
      document.getElementById("piano-switch").className = "";
    }
  }

  // Handle volume change
  handleVolume(event) {
    this.setState({
      volume: event.target.value
    });
  }

  render() {
    return (
      <div>
        <div id="loading-status">
          {this.state.showStart && <Start startDrums={this.startDrums} />}
          {this.state.loading && LOADING_SPINNER}
          {this.state.powerOn && INSTRUCTIONS}
        </div>
        <div id="drum-machine" className="container text-center">
          <Display currentDrum={this.state.currentDrum} />
          <DrumButtons handleDrumClick={this.handleDrumClick} kitData={this.state.kitData} />
          <Controls
            handleKitSwitch={this.handleKitSwitch}
            handleVolume={this.handleVolume}
            currentKit={this.state.currentKit}
            volume={this.state.volume}
          />
        </div>
      </div>
    );
  }
} // end of DrumMachineApp parent component

// Drum buttons - child of parent App
function DrumButtons(props) {
  // Map through drum bank array to render HTML
  const drumPad = props.kitData.map(drum => (
    <div
      id={drum.clipName}
      key={drum.keyTrigger}
      data-value={drum.keyTrigger}
      className="drum-pad col"
      onClick={props.handleDrumClick}>
      {drum.keyTrigger}
      <audio id={drum.keyTrigger} name={drum.displayName} className="clip" src={drum.url} type="audio/mp3" />
    </div>
  ));

  return (
    <div>
      <div className="row no-gutters justify-content-center">
        {drumPad[0]}
        {drumPad[1]}
        {drumPad[2]}
      </div>
      <div className="row no-gutters justify-content-center">
        {drumPad[3]}
        {drumPad[4]}
        {drumPad[5]}
      </div>
      <div className="row no-gutters justify-content-center">
        {drumPad[6]}
        {drumPad[7]}
        {drumPad[8]}
      </div>
    </div>
  );
}

// Display name of current drum - child of parent App
function Display(props) {
  return (
    <div className="row justify-content-center">
      <div id="display">
        {/* If there is no drum present then add spaces so it will render */}
        {props.currentDrum === "" ? "\u00a0\u00a0" : props.currentDrum}
      </div>
    </div>
  );
}

// Volume and Bank controls - child of parent App
function Controls(props) {
  return (
    <div>
      <div id="kit-switches">
        <p>Kit</p>
        <div>
          <button id="heater-switch" className="active" value="Heater Kit" onClick={props.handleKitSwitch}>
            Heater
          </button>
          <button id="piano-switch" className="" value="Smooth Piano Kit" onClick={props.handleKitSwitch}>
            Smooth Piano
          </button>
        </div>
      </div>

      <div id="volume-controls">
        <div>
          <label htmlFor="volume">Volume</label>
        </div>
        <input type="range" id="volume-control" name="volume" value={props.volume} onChange={props.handleVolume} />
      </div>
    </div>
  );
} // end Controls()

function Start(props) {
  return (
    <div>
      {" "}
      <button id="start-button" onClick={props.startDrums}>
        Start
      </button>
    </div>
  );
}

const INSTRUCTIONS = <div id="instructions">Tap drums with mouse or keyboard</div>;

// Loading Spinner JSX HTML
const LOADING_SPINNER = (
  <div id="loading-bar" className="sk-circle">
    <div className="sk-circle1 sk-child" />
    <div className="sk-circle2 sk-child" />
    <div className="sk-circle3 sk-child" />
    <div className="sk-circle4 sk-child" />
    <div className="sk-circle5 sk-child" />
    <div className="sk-circle6 sk-child" />
    <div className="sk-circle7 sk-child" />
    <div className="sk-circle8 sk-child" />
    <div className="sk-circle9 sk-child" />
    <div className="sk-circle10 sk-child" />
    <div className="sk-circle11 sk-child" />
    <div className="sk-circle12 sk-child" />
  </div>
);

export default DrumMachineApp;
